.disable-select {
  user-select: none;
  /* supported by Chrome and Opera */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
}

h3 {
  color: white;
  font-weight: normal;
  font-size: 1.4rem;
  letter-spacing: 0.05rem;
  margin: 0;
}

p {
  color: #616060;
  font-size: 0.9rem;
  margin: 7px;
}

.desc {
  margin-top: 20px;
  margin-left: 15px;
  margin-right: 15px;
}

.greyCard {
  background: white;
  color: white;
  height: 390px;
  width: 280px;
  border-radius: 39px;
}

.miau-card {
  height: 390px;
  width: 280px;
  border-radius: 39px;
  position: relative;
  top: -80px;
}

.content {
  margin-top: 45px;
}

.kitty-img {
  border-radius: 30px 30px;
  padding: 10px;
  background-color: white;
  box-shadow: 0 0 5px #3838388e;
  width: 100px;
  height: 100px;
  position: absolute;
  top: 60px;
  left: 50%;
  transform: translateX(-50%)
}

.pic-background {
  background-image: linear-gradient(to bottom, #88334f, #9b415b, #a4465f, #ae4c62, #b75265);
  border-radius: 30px 30px 0 0;
  padding: 20px;
  height: 150px;
}

.stats {
  display: flex;
  justify-content: space-evenly;
}

.stat-value {
  color: #b75265;
  font-weight: bold;
  font-size: 1.7rem;
}

.stat-title {
  font-size: 0.7rem;
  color: #909090;
  ;
}